@use 'global' as * ; 

.sub-about-top{
  padding-bottom: rem(60);
}

.sub-about-top__header{
  position: relative;
}

.sub-about-top__title {
  position: absolute;
  top: rem(20);
  left: calc(50% - rem(167));
  z-index: 3;

}

.sub-about-top__img {
  margin: 0 calc(50% - 50vw);
  width: 100vw;
  position: relative;
  height: rem(281);
  z-index: -1;
}

  
  .sub-about-top__img img {
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
  }
.sub-about-top__text-box{
  width: 100%;
  padding: rem(20);
  background: $white;
  margin-top: rem(-50);
   box-shadow: 0 3px 12px;


 }
.suv-about-top__sub-title{
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1.45;
  text-align: center;
  color: $color-main-dark2;

}
.suv-about-top__sub-text{
  margin-top: rem(20);
}
